.title {
    font-size: 20px;
}

.forms_wrapper {
    flex: 0 1 300px;
}

.flex {
    display: flex;
    gap: 20px;
}

.form {
    border: 2px solid #6c7ae0;
    padding: 0 15px 10px;
    min-width: 320px;
}

.form span {
    display: flex;
    padding: 7px 0;
}

.table {
    display: grid;
    border-collapse: collapse;
    min-width: 100%;
}

.table.products {

    grid-template-columns: minmax(150px, 1fr)
    minmax(150px, 1.67fr)
    minmax(150px, 1.67fr)
    minmax(150px, 1.67fr)
    minmax(150px, 1.67fr)
    minmax(150px, 1.67fr)
    minmax(150px, 1.67fr);

    grid-template-rows: 50px auto;

}

.table.categories {
    grid-template-columns: minmax(150px, 1fr)
    minmax(150px, 1.67fr)
    minmax(150px, 1.67fr)
    minmax(150px, 1.67fr)
    minmax(150px, 0.33fr);
}

thead,
tbody,
tr {
    display: contents;
}

th,
td {
    padding: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-flex;
    gap: 10px;
    align-items: center;
}

th {
    position: sticky;
    top: 0;
    @apply bg-blue-600;
    text-align: left;
    font-weight: normal;
    font-size: 1.1rem;
    color: white;
}

th:last-child {
    border: 0;
}

td {
    padding-top: 10px;
    padding-bottom: 10px;
    color: #000000;
    font-size: 18px;
}

tr:nth-child(even) td {
    background: #f8f6ff;
}

.category_feature {
    gap: 10px;
    display: inline-flex;
}

.category_feature span {
    padding: 3px;
    color: #fff;
    background-color: #ff9353;
}

.form {
    display: flex;
    flex-direction: column;
    max-width: 200px;
    gap: 5px;
}

.col_descr {
    font-size: 12px;
}

.btn_blue {
    @apply bg-blue-500 hover:bg-blue-400 text-white font-bold py-1 px-3 border-b-4 border-blue-700 hover:border-blue-500 rounded text-sm
}

.btn_green {
    @apply bg-green-600 hover:bg-green-400 text-white font-bold py-1 px-3 border-b-4 border-green-700 hover:border-green-500 rounded text-sm;
}

.btn_gray {
    @apply bg-gray-400 hover:bg-gray-500 text-white font-bold py-1 px-3 border-b-4 border-gray-500 hover:border-gray-600 rounded text-sm;
}

.btn_orange {
    @apply bg-orange-500 hover:bg-orange-600 text-white font-bold py-1 px-3 border-b-4 border-orange-700 hover:border-orange-700 rounded text-sm;
}

.btn_red {
    @apply bg-red-500 hover:bg-red-600 text-white font-bold py-1 px-3 border-b-4 border-red-700 hover:border-red-700 rounded text-sm;
}